<template>
  <div class="home">
    <svg id="map" version="1.1" class="board" viewBox="0 0 630 420" preserveAspectRatio="none">
      <image width="640" height="420" xlink:href="../assets/feud.png" />

      <g :class="{ visible: answer1 !== '' }">
        <rect x="115" y="160" width="195" height="45" fill="#4fa9f1" />
        <text x="120" y="190" font-family="Arial" font-size="20" fill="white">{{ answer1 }}</text>
      </g>

      <g :class="{ visible: answer2 !== '' }">
        <rect x="115" y="225" width="195" height="45" fill="#4fa9f1" />
        <text x="120" y="255" font-family="Arial" font-size="20" fill="white">{{ answer2 }}</text>
      </g>

      <g :class="{ visible: answer3 !== '' }">
        <rect x="115" y="290" width="195" height="45" fill="#4fa9f1" />
        <text x="120" y="320" font-family="Arial" font-size="20" fill="white">{{ answer3}}</text>
      </g>

      <g :class="{ visible: answer4 !== '' }">
        <rect x="115" y="355" width="195" height="45" fill="#4fa9f1" />
        <text x="120" y="385" font-family="Arial" font-size="20" fill="white">{{ answer4 }}</text>
      </g>

      <g :class="{ visible: answer5 !== '' }">
        <rect x="330" y="160" width="195" height="45" fill="#4fa9f1" />
        <text x="335" y="190" font-family="Arial" font-size="20" fill="white">{{ answer5}}</text>
      </g>

      <g :class="{ visible: answer6 !== '' }">
        <rect x="330" y="225" width="195" height="45" fill="#4fa9f1" />
        <text x="335" y="255" font-family="Arial" font-size="20" fill="white">{{ answer6 }}</text>
      </g>

      <g :class="{ visible: answer7 !== '' }">
        <rect x="330" y="290" width="195" height="45" fill="#4fa9f1" />
        <text x="335" y="320" font-family="Arial" font-size="20" fill="white">{{ answer7}}</text>
      </g>

      <g :class="{ visible: answer8 !== '' }">
        <rect x="330" y="355" width="195" height="45" fill="#4fa9f1" />
        <text x="335" y="385" font-family="Arial" font-size="20" fill="white">{{ answer8 }}</text>
      </g>

      <g class="visible">
        <rect x="255" y="40" width="130" height="90" fill="#4fa9f1" />
        <text
          v-bind:x="score === 0 ? 315 : 305"
          y="95"
          width="130"
          height="90"
          font-family="Arial"
          font-size="30"
          fill="white"
        >{{ score }}</text>
      </g>
    </svg>

    <div class="incorrect-image-container" :class="{ visible: incorrectVisible }">
      <img src="../assets/x.png" v-for="n in numWrong" :key="n" />
      {{numWrong}}
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  components: {},
  data: function() {
    return {
      incorrectVisible: false,
      doublePoints: false,
      numWrong: 0,
      answer1: "",
      answer2: "",
      answer3: "",
      answer4: "",
      answer5: "",
      answer6: "",
      answer7: "",
      answer8: ""
    };
  },
  computed: {
    score: function() {
      let score = 0;
      // Oof
      if (this.answer1) {
        score += 10;
      }
      if (this.answer2) {
        score += 10;
      }
      if (this.answer3) {
        score += 10;
      }
      if (this.answer4) {
        score += 10;
      }
      if (this.answer5) {
        score += 10;
      }
      if (this.answer6) {
        score += 10;
      }
      if (this.answer7) {
        score += 10;
      }
      if (this.answer8) {
        score += 10;
      }

      if (this.doublePoints) {
        return score * 2;
      }

      return score;
    }
  },
  mounted: function() {
    var bc = new BroadcastChannel("feud");

    bc.onmessage = e => {
      if (e.data === "incorrect") {
        this.showIncorrect();
      } else if (e.data === "clear") {
        this.handleClear();
      } else {
        this.showCorrect(e.data);
      }
    };
  },
  methods: {
    showIncorrect: function() {
      this.numWrong = this.numWrong + 1;
      if (this.numWrong > 3) {
        this.numWrong = 1;
      }

      this.incorrectVisible = true;

      setTimeout(() => {
        this.incorrectVisible = false;
      }, 1300);
    },
    showCorrect: function({ message, answerIndex, doublePoints }) {
      const key = `answer${answerIndex + 1}`;
      this[key] = message;
      this.doublePoints = doublePoints;
    },
    handleClear: function() {
      this.numWrong = 0;
      this.doublePoints = false;
      this.answer1 = "";
      this.answer2 = "";
      this.answer3 = "";
      this.answer4 = "";
      this.answer5 = "";
      this.answer6 = "";
      this.answer7 = "";
      this.answer8 = "";
    }
  }
};
</script>

<style scoped>
.home {
  padding: 0 10vw;
  background: black;
}
.board {
  display: block;
  width: 100%;
  height: 100vh;
}

.incorrect-image-container {
  display: none;
  max-width: 100%;
}
.incorrect-image-container.visible {
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
/* For answers */
svg g {
  display: none;
  transform: rotateX(-90deg);
  transition: 1s all;
}
svg g.visible {
  display: block;
  transform: rotateX(0deg);
}

/* Couldn't figure out why svg g:nth-child() wasnt working so here we are with 1 offset */
svg :nth-child(2) {
  transform-origin: 0 160px;
}
svg :nth-child(3) {
  transform-origin: 0 225px;
}
svg :nth-child(4) {
  transform-origin: 0 290px;
}
svg :nth-child(5) {
  transform-origin: 0 335px;
}
svg :nth-child(6) {
  transform-origin: 0 160px;
}
svg :nth-child(7) {
  transform-origin: 0 225px;
}
svg :nth-child(8) {
  transform-origin: 0 290px;
}
svg :nth-child(9) {
  transform-origin: 0 335px;
}
</style>
