<template>
  <div class="home">
    <section>
      <figure>
        <figcaption>Theme</figcaption>
        <audio controls src="../assets/theme.mp3"></audio>
      </figure>

      <figure>
        <figcaption>Correct</figcaption>
        <audio id="correct" controls src="../assets/correct.mp3"></audio>
      </figure>

      <figure>
        <figcaption>Incorrect</figcaption>
        <audio id="incorrect" controls src="../assets/incorrect.mp3"></audio>
      </figure>

      <figure>
        <figcaption>Steal</figcaption>
        <audio id="steal" controls src="../assets/steal.mp3"></audio>
      </figure>

      <figure>
        <figcaption>Jeopardy</figcaption>
        <audio id="jeopardy" controls src="../assets/jeopardy.mp3"></audio>
      </figure>

      <input type="checkbox" id="double-points" v-model="doublePoints" />
      <label for="double-points">Double Points</label>
    </section>

    <section class="questions" v-for="(question, index) in questions" :key="question.question">
      <h3>Question {{index + 1}}</h3>
      <h3>{{ question.question }}</h3>
      <ol>
        <li v-for="(answer, index) in question.answers" :key="answer">
          <button @click="handleCorrectClick(index, answer)">{{ answer }}</button>
        </li>
      </ol>
      <div class="extra-controls">
        <button @click="handleIncorrectClick">Incorrect</button>
        <button @click="handleClearClick">Clear</button>
        <button @click="handleStealClick">Steal</button>
      </div>
    </section>
  </div>
</template>

<script>
import Airtable from "airtable";

function configureAirtable() {
  var base = new Airtable({ apiKey: "keyeOgVEiPQnEVfFz" }).base(
    "app0HOMKuC03nJtR9"
  );

  let results = [];

  return new Promise(function(resolve, reject) {
    base("Questions")
      .select({
        view: "Grid view"
      })
      .eachPage(
        function page(records, fetchNextPage) {
          // This function (`page`) will get called for each page of records.

          records.forEach(function(record) {
            results.push(record.fields);
          });

          // To fetch the next page of records, call `fetchNextPage`.
          // If there are more records, `page` will get called again.
          // If there are no more records, `done` will get called.
          fetchNextPage();
        },
        function done(err) {
          console.log("in done");
          if (err) {
            console.error(err);
            reject(err);
            return;
          }
          resolve(results);
        }
      );
  });
}

export default {
  name: "Home",
  components: {},
  data: function() {
    return {
      questions: [],
      doublePoints: false
    };
  },
  mounted: function() {
    configureAirtable().then(results => {
      // Create new sub-arrays for answers for each question
      const formattedResults = results.map((key, value) => {
        let question;
        let answers = [];

        const resultsArrayItem = results[value];

        Object.keys(resultsArrayItem).forEach(arrayKey => {
          if (arrayKey === "question") {
            question = resultsArrayItem[arrayKey];
          } else {
            const index = parseInt(arrayKey.split("answer")[1]) - 1;
            answers[index] = resultsArrayItem[arrayKey];
          }
        });

        return {
          question: question,
          answers: answers
        };
      });

      this.questions = formattedResults;
    });

    this.channel = new BroadcastChannel("feud");
  },
  methods: {
    handleIncorrectClick: function() {
      document.querySelector("#incorrect").play();
      this.channel.postMessage("incorrect");
    },
    handleClearClick: function() {
      this.channel.postMessage("clear");
    },
    handleStealClick: function() {
      const element = document.querySelector("#steal");
      if (element.paused) {
        element.play();
      } else {
        element.pause();
        element.currentTime = 0;
      }
    },
    handleCorrectClick: function(answerIndex, message) {
      document.querySelector("#correct").play();
      this.channel.postMessage({
        answerIndex,
        message,
        doublePoints: this.doublePoints
      });
    }
  }
};
</script>

